<template>
  <div :class="['postcomment-item', { active: isHigh }]" :id="id">
    <router-link :to="`/user/${author.loginname}`">
      <el-avatar
        class="comment-avatar"
        shape="square"
        size="small"
        :src="author.avatar_url"
      ></el-avatar>
    </router-link>
    <div class="comment-right">
      <div class="header">
        <span>{{ author.loginname }}</span>
        <span class="postition">
          <a :href="`#${id}`"
            >{{ index + 1 }}楼 · {{ create_at | moment("from", "now") }}</a
          >
        </span>
        <span @click="upsClick" class="like">
          <i v-if="is_uped" class="iconfont icon-dianzan_kuai"></i>
          <i v-else class="iconfont icon-31dianzan"></i>
          {{ ups.length ? ups.length :'' }}</span>
      </div>
      <div v-html="content"></div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  props: ["id", "author", "index", "create_at", "content", "ups",'is_uped'],
  computed: {
    isHigh() {
      return this.ups.length >= 10 ? true : false;
    },
  },
  methods: {
    ...mapActions(['upsHandle']),
    upsClick(){
      this.upsHandle(this.id)
    }
  },
};
</script>

<style lang='less'>
.postcomment-item {
  border-bottom: 1px solid #ccc;
  padding-top: 10px;
  &.active {
    background-color: #f4fcf0;
  }
  // border-top: 1px solid #ccc;
  width: 100%;
  display: flex;
  .comment-right {
    margin-left: 10px;
    flex-grow: 1;
    .header {
      font-size: 12px;
      display: flex;
      .postition {
        color: blue;
        flex-grow: 1;
      }
    }

    img {
      width: 100%;
    }
  }
}
</style>