<template>
  <div v-if="Topic" class="title-page">
    <Panel>
      <template #content>
        <div class="post-header">
          <h2>
            <span class="tag" v-if="Topic.top || Topic.good">{{
              Topic.top ? "置顶" : "精华"
            }}</span>
            {{ Topic.title }}
          </h2>
          <div>
            <p>
              <span class="disc">·</span>
              发布于
              <span>
                {{ Topic.create_at | moment("from", "now") }}
              </span>
              <span class="disc">·</span>
              <span> 作者{{ Topic.author.loginname }} </span>
              <span class="disc">·</span>
              <span> {{ Topic.visit_count }} </span>次浏览
              <span class="disc">·</span>
              来自<span>{{ Topic.tab | transformTab }}</span>
            </p>
            <el-button @click='collect' :type="Topic.is_collect ? 'info':'success'" size="mini" v-if="Info">{{Topic.is_collect ? '取消收藏':'收藏'}}</el-button>
          </div>
        </div>
        <div class="post-content" v-html="Topic.content"></div>
      </template>
    </Panel>

    <Panel v-if="Topic.replies.length">
      <template #header>
        <span>{{ Topic.reply_count }}回复</span>
      </template>
      <template #content>
        <TopicCommmentItem
          v-for="(comment, index) in Topic.replies"
          :key="comment.id"
          v-bind="{ ...comment, index }"
        />
      </template>
    </Panel>

    <Panel v-if="Info">
      <template #header>
        <span>添加回复</span>
        </template> 
        <template #content>
          <textarea v-model="content" name="" id="" cols="30" rows="10"></textarea>
          <el-button @click="add" type='primary'>回复</el-button>
        </template>
    </Panel>
  </div>
  <el-skeleton v-else :rows="8" animated />
</template>

<script>
import { mapActions, mapState } from "vuex";
import TopicCommmentItem from "../components/TopicCommmentItem.vue";
import Panel from "../containers/Panel.vue";
export default {
  components: { Panel, TopicCommmentItem },
  data() {
    return {
      content: ''
    }
  },
  computed: {
    ...mapState(["TopicPost", "login"]),
    Topic() {
      return this.TopicPost.Topic;
    },
    Info() {
      return this.login.info;
    },
  },
  created() {
    this.getTopicPost(this.$route.params.id);
    //   console.log(this.Topic);
  },
  methods: {
    ...mapActions(["getTopicPost",'collectHandle','addComment']),
    collect(){
      const {id,is_collect} = this.Topic
      this.collectHandle({id:id,is_collect:!is_collect})
    },
    add(){
      if(this.content.trim()){
        this.addComment({id:this.Topic.id,content:this.content})
      }
      
    }
  },
};
</script>

<style lang='less'>
.title-page {
  .post-header {
    >div{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    h2 {
      width: 75%;
      .tag {
        font-size: 12px;
        color: #fff;
        padding: 2px 4px;
        background-color: #80bd01;

        border-radius: 3px;
      }
    }
    p {
      font-size: 14px;
      .disc {
        margin: 0 10px;
        font-weight: bold;
      }
    }
  }
  .post-content {
    img {
      width: 100%;
    }
  }
}
</style>